.selector {
    position: relative;
    display: flex;
    justify-content: space-around;
    width: 20vw;
    height: 20vw;
    max-width: 25vh;
    max-height: 25vh;
    border-radius: 8px;
    padding: 1.5rem;
    box-shadow: 0px 0px 7px 1px rgba(0, 0, 0, 0.4);
}

.selector h3 {
    position: absolute;
    bottom: 10px;
}

.selector-img {
    position: absolute;
    width: 75%;
    height: 75%;
}

.game-link {
    text-decoration: none;
    font-size: 1.2rem;
    color: black;
}
