nav {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    top: 0;
    width: 100%;
    height: 60px;
    margin: 0 0 2rem 0;
    background-color: #34b7eb;
    z-index: 100;
}

li {
    display: inline-block;
    margin: 1.5rem;
}

.link {
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
}
